import React from 'react';
import { useForm } from 'react-hook-form'

const EditUserForm = (props) => {

    const {register, formState: { errors }, setValue, handleSubmit} = useForm({
        defaultValues:props.currentUser
    });
    setValue('name', props.currentUser.name);
    setValue('profesion', props.currentUser.profesion);

    const ejecutar = (data) => {
        //console.log(data)
        data.id = props.currentUser.id
        props.updateUser(props.currentUser.id, data)

    }


    return (
        <form onSubmit={handleSubmit(ejecutar)}>
            <label>Name</label>
            <input type="text" {...register("name", { required: true})}/>
            {errors.name && "Campo requerido"}
            <label>Profesion</label>
            <input type="text" {...register("profesion", { required: true})}/>
            {errors.profesion && "Campo requerido"}
            <br></br>
            <button>Edit user</button>
        </form>
     );
}

export default EditUserForm;