import React from 'react';
import { useForm } from 'react-hook-form' //Formulario enlazado

const AddUserForm = (props) => {

    const {register, formState: { errors }, handleSubmit} = useForm();

    const ejecutar = (data, e) => {

        props.addUser(data)
        // limpiar campos
    e.target.reset();
    }


    return (
        <form onSubmit={handleSubmit(ejecutar)}>
            <label>Nombre y Apellidos</label>
            <br/>
            <input class="input" type="text" {...register("name", { required: true})}/>
            {errors.name && "Campo requerido"} <br/><br/>

            <label>Profesion</label><br/>
            <input class="input" type="text" {...register("profesion", { required: true})}/>
            {errors.profesion && "Campo requerido"}<br/>
            <br></br>
            <button class="boton">Agregar nuevo usuario</button>
        </form>
     );
}

export default AddUserForm;