import React, {useState}  from 'react';
import Tabla from "./components/Tabla";
import{v4 as uuidv4} from 'uuid';
import AddUserForm from './components/AddUserForm';
import EditUserForm from './components/EditUserForm';



function App() {

  const datosUsuarios = [
    {id: uuidv4(), name: 'Alejo', profesion:'alejosoft'},
    {id: uuidv4(), name: 'camila', profesion:'camimci'},
    {id: uuidv4(), name: 'mauro', profesion:'asds'},
  ]

  // state
  const [users, setUsers] = useState(datosUsuarios)

  // agregar usuario
  const addUser = (user) => {
    user.id = uuidv4()
    setUsers([
      ...users,
      user
    ])
  }

  // eliminar usuario
  const deleteUser = (id)=>{
    const arrayfiltrado = users.filter(user => user.id !== id);
    setUsers(arrayfiltrado)
  }
// editar usuarios
  const [editing, setEditing]=useState(false);
  const [currentUser, setCurrentUser] = useState ({
    id: null, name:'', profesion: ''
  });

  const editRow = (user) => {
    setEditing(true);
    setCurrentUser({
      id: user.id, name: user.name, profesion: user.profesion
    })
  }

  const updateUser = (id, updatedUser) => {
    setEditing(false);
    setUsers(users.map(user => (user.id === id? updatedUser: user)))
  }
  return (
    <div className="container">
      <h1>Actividad Final</h1>
      <div className="flex-row">
        <div className="flex-large">
          {
            editing?(
              <div>
                <h2>Editar usuario</h2>
                <EditUserForm
                  currentUser= {currentUser}
                  updateUser = {updateUser}
                />
              </div>
            ): (
              <div>
                <h2>Agregar usuario</h2>
                <AddUserForm addUser={addUser} />
              </div>

            )
          }


        </div>
        <div className="flex-large">
          <h2>Ver usuarios</h2>
          <Tabla
          users={users}
          deleteUser={deleteUser}
          setEditing={setEditing}
          editRow={editRow}
           />

        </div>
      </div>
    </div>
  );
}

export default App;
